@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body,
#root,
.App {
  text-align: center;
  height: 100%;
  width: 100%;
  font-family: "Roboto";
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#components-layout-demo-responsive .logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px;
}

.site-layout-sub-header-background {
  background: #fff;
}

.site-layout-background {
  background: #fff;
}

div.loginCardStyle {
  border-radius: 20px;
  box-shadow: rgba(0, 0, 0, 0.1) 0 0 6px;
  text-align: center;
  width: 360px;
}
div.registerCardStyle {
  border-radius: 20px;
  box-shadow: rgba(0, 0, 0, 0.1) 0 0 6px;
  width: 360px;
  height: 620px;
}

div.icons-list {
  text-align: center;
  font-size: 40px;
}

Button.form-button {
  background-color: #55c596;
  border-color: #55c596;
  width: 300px;
}

div Button.modal-button-edit {
  border-radius: 20px;
  background-color: #0275d8;
  border-color: #0275d8;
}

div Button.modal-button-upload {
  border-radius: 20px;
  background-color: #5bc0de;
  border-color: #5bc0de;
}

div Button.modal-button {
  border-radius: 20px;
}

div Button.modal-button-view {
  border-radius: 20px;
  background-color: #5cb85c;
  border-color: #5cb85c;
}

div Button.modal-button-add {
  border-radius: 20px;
  background-color: #55c596;
  border-color: #55c596;
}

Button span.mobile-view {
  display: none;
}

@media screen and (max-width: 768px) {
  Button span.desktop-view {
    display: none;
  }
  Button span.mobile-view {
    display: inline-block;
  }
}

.sidebar {
  height: 100vh;
}

